import React from 'react';
import { Label, Text, Tag } from 'react-konva';
import Konva from 'konva';
import KonvaEventObject = Konva.KonvaEventObject;
import {IButton} from '../types/__types';

const stopPropagation = (evt:KonvaEventObject<MouseEvent>) => {
    evt.cancelBubble = true;
};

const CloseButton = (props: IButton) => {
    const handleClick = () => {
        props.handleClick(props.id);
    };

    const width = 40;
    const height = 40;
    return (
        <Label
            key="label"
            x={props.x - width / 2}
            y={props.y - height / 2}
            onClick={handleClick}
            onTap={handleClick}
            onMouseDown={stopPropagation}
            onMouseUp={stopPropagation}
            onMouseMove={stopPropagation}
            width={width}
            height={height}
            opacity={0.5}
        >
            <Tag
                fill="black"
                lineJoin="round"
                shadowColor="black"
                cornerRadius={width / 2}

            />
            <Text
                text="X"
                fill="white"
                padding={15}
                onClick={handleClick}
                onTap={handleClick}
            />
        </Label>
    );
};


export default CloseButton;
