import React, { useState } from 'react';
import { Stage, Layer, Rect } from 'react-konva';
import Konva from 'konva';
import KonvaEventObject = Konva.KonvaEventObject;
import {IProps, IShelve} from '../types/__types';
import CloseButton from './CloseButton';

const MainComponent = (props:IProps) => {
    const [newShelve, setNewShelve] = useState<IShelve | null>(null);

    const handleMouseDown = (evt:KonvaEventObject<MouseEvent>) => {
        const position = evt.target.getStage()?.getPointerPosition();
        if (!newShelve && position) {
            const { x, y } = position;
            setNewShelve({ id: 0, x, y, width: 0, height: 0 });
        }
    };

    const handleMouseUp = (evt:KonvaEventObject<MouseEvent>) => {
        const position = evt.target.getStage()?.getPointerPosition();
        if (newShelve && position) {
            const { shelves } = props;
            const sx = newShelve.x;
            const sy = newShelve.y;
            const { x, y } = position;
            const annotationToAdd = {
                id: shelves.length + 1,
                x: sx,
                y: sy,
                width: x - sx,
                height: y - sy
            };
            setNewShelve(null);
            const newShelves = [...shelves];
            newShelves.push(annotationToAdd);
            props.onChange(newShelves);
        }
    };

    const deleteShelve = (id:number) => {
        const { shelves } = props;
        props.onChange(shelves.filter((shelve:IShelve) => shelve.id !== id));
    };

    const handleMouseMove = (evt:KonvaEventObject<MouseEvent>) => {
        const position = evt.target.getStage()?.getPointerPosition();
        if (newShelve && position) {
            const sx = newShelve.x;
            const sy = newShelve.y;
            const { x, y } = position;
            setNewShelve({
                id: 0,
                x: sx,
                y: sy,
                width: x - sx,
                height: y - sy
            });
        }
    };

    const shelvesToDraw = [...props.shelves].concat(newShelve ? newShelve : []);
    return (
        <Stage
            className="stage"
            onMouseDown={handleMouseDown}
            onMouseUp={handleMouseUp}
            onMouseMove={handleMouseMove}
            width={1000}
            height={800}
        >
            <Layer>
                {shelvesToDraw.map((shelve:IShelve) => {
                    return [
                        <Rect
                            key="rect"
                            x={shelve.x}
                            y={shelve.y}
                            width={shelve.width}
                            height={shelve.height}
                            stroke="black"
                        />,
                        <CloseButton
                            key="close"
                            id={shelve.id}
                            x={shelve.x + shelve.width / 2}
                            y={shelve.y + shelve.height / 2}
                            handleClick={deleteShelve}
                        />
                    ];
                })}
            </Layer>
        </Stage>
    );
};


export default MainComponent;
